.prospect object { -o-object-fit: contain !important; object-fit: contain !important; }

.prospect .content {
  border-radius: 10px;
  background-color: whitesmoke;
  -webkit-box-shadow: 0 4px 5px 0 rgba(0,0,0,0.14), 0 1px 10px 0 rgba(0,0,0,0.12), 0 2px 4px -1px rgba(0,0,0,0.3);
          box-shadow: 0 4px 5px 0 rgba(0,0,0,0.14), 0 1px 10px 0 rgba(0,0,0,0.12), 0 2px 4px -1px rgba(0,0,0,0.3);
}

.prospect .img, .prospect .docs {
  color: white;
  margin-left: 0;
  margin-right: 0;
  margin-bottom: 0;
  -webkit-box-shadow: 0px 1px 3px 0px #000A;
          box-shadow: 0px 1px 3px 0px #000A;
  border-radius: 10px 10px 25px 25px;
}

.prospect .remove { position: absolute; right: 10%; }

.prospect .data { margin-left: 0; margin-right: 0; padding: 20px 0; }

.prospect .data .info { display: block; font-size: 12px; margin-top: -10px; }

.prospect .halfway { position: relative; float: right; margin-right: 20px; margin-top: -30px; }

.hello { top: 50px; left: 0; width: 100vw; position: absolute; }

.hello .section { color: white; position: relative; background-color: var(--submain); }

.hello h1 {
  left: 50%;
  top: 200px;
  padding: 10px;
  color: white;
  margin: 0 auto;
  position: fixed;
  font-weight: bold;
  font-size: 3em;
  text-align: center;
  mix-blend-mode: overlay;
  background-color: var(--error);
  transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
}

.hello .card {
  padding-top: 30px;
  border-width: 2px;
  border-radius: 5px;
  text-align: center;
  border-style: solid;
  padding-bottom: 30px;
  padding: 0 !important;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border-color: transparent;
  background-color: var(--main);
}

.hello .card .icon { padding-top: 30px; padding-bottom: 30px; }

.hello .card:hover .icon i { -webkit-transform: scale(1.2); transform: scale(1.2); }

