.hello .card .icon i { color: white; border-radius: 50%; position: relative; background-color: var(--warning); }

.hello .card .text p { margin: 0 !important; padding: 20px; font-size: 25px; color: whitesmoke; background: #000A; font-weight: bold; }

/*************************************************************************/

/******************************** Loader *********************************/

/*************************************************************************/

.loader { top: 0; left: 0; z-index: 1100; width: 100%; height: 100%; position: fixed; color: whitesmoke; background: #141E3099; }

.loader h5 { position: absolute; top: 50%; left: 50%; -webkit-transform: translate(-50%, -50%); transform: translate(-50%, -50%); margin: 0 !important; }

.loader svg { position: absolute; top: 50%; left: 50%; -webkit-transform: translate(-50%, -50%) rotate(45deg); transform: translate(-50%, -50%) rotate(45deg); background: #000B; }

.stroke-still { stroke: #232323; }

.stroke-animation {
  -webkit-animation: stroke-spacing 0.6s ease-in, stroke-color 2.4s linear;
          animation: stroke-spacing 0.6s ease-in, stroke-color 2.4s linear;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-delay: 0;
          animation-delay: 0;
  -webkit-animation-direction: normal;
          animation-direction: normal;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-play-state: running;
          animation-play-state: running;
  -webkit-transform-origin: center center;
          transform-origin: center center;
}

@-webkit-keyframes stroke-spacing {
  0% { stroke-dasharray: 0 200; }
  45% { stroke-dashoffset: 0; stroke-dasharray: 200 200; }
  90% { stroke-dashoffset: -200; stroke-dasharray: 200 200; }
  100% { stroke-dashoffset: -200; stroke-dasharray: 200 200;}
}

@keyframes stroke-spacing {
  0% { stroke-dasharray: 0 200; }
  45% { stroke-dashoffset: 0; stroke-dasharray: 200 200; }
  90% { stroke-dashoffset: -200; stroke-dasharray: 200 200; }
  100% { stroke-dashoffset: -200; stroke-dasharray: 200 200; }
}

@-webkit-keyframes stroke-color {
  0%  { stroke: #3498DB; }
  24% { stroke: #643232; }
  25% { stroke: #327864; }
  49% { stroke: #327864; }
  50% { stroke: #32326e; }
  74% { stroke: #32326e; }
  75% { stroke: #78325a; }
  99% { stroke: #78325a; }
}

@keyframes stroke-color {
  0%  { stroke: #3498DB; }
  24% { stroke: #643232; }
  25% { stroke: #327864; }
  49% { stroke: #327864; }
  50% { stroke: #32326e; }
  74% { stroke: #32326e; }
  75% { stroke: #78325a; }
  99% { stroke: #78325a; }
}
