.btn i, .btn-floating i { text-shadow: 0px 0px 2px black; }
.badge { color: white !important; border: 2px solid whitesmoke; border-radius: 5px; float: none !important; -webkit-box-sizing: content-box !important; box-sizing: content-box !important; -webkit-box-shadow: 0 0 1px 1px #0003; box-shadow: 0 0 1px 1px #0003; }
td, th { border-radius: 0px; }
p, h1, h2, h3, h4, h5, h6 { margin: 10px 0 !important; }
